body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.stars-block {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 0 21px;
  display: flex;
}

.stars-block .stars {
  display: flex;
}

.stars-block .stars__star {
  background-image: url("star.822d561e.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars-block .stars .stars__star:nth-child(n+5) {
  margin-right: 0;
}

.stars-block .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
}

.stars-block__span {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  margin-bottom: 41px;
}

.card__title {
  color: #000;
  margin: 0 0 5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  width: 100%;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__link {
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.price-block {
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
  height: fit-content;
  margin: 0 0 16px;
  padding: 0;
  display: flex;
}

.price-block__text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price-block__price {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
